/* DarkModeToggle.css */
.dark-mode-toggle {
  width: 80px;
  height: 40px;
  border: none;
  outline: none;
  cursor: pointer;
  background: transparent;
  padding: 0;
  position: relative;
  transition: transform 0.2s ease;
}

.dark-mode-toggle:hover {
  transform: scale(1.05);
}

.toggle-track {
  width: 100%;
  height: 100%;
  border-radius: 20px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border: 3px solid #8b9dc3;
  position: relative;
  transition: all 0.4s cubic-bezier(0.25, 0.8, 0.25, 1);
  box-shadow: 
    0 4px 12px rgba(102, 126, 234, 0.4),
    inset 0 2px 4px rgba(255, 255, 255, 0.1);
}

.dark-mode-toggle.light .toggle-track {
  background: linear-gradient(135deg, #a8b8e0 0%, #c7d2fe 100%);
  border-color: #b8c5e0;
  box-shadow: 
    0 4px 12px rgba(168, 184, 224, 0.3),
    inset 0 2px 4px rgba(255, 255, 255, 0.2);
}

.toggle-thumb {
  width: 32px;
  height: 32px;
  background: #1e293b;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 4px;
  transform: translateY(-50%);
  transition: all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 
    0 2px 8px rgba(0, 0, 0, 0.3),
    0 1px 4px rgba(0, 0, 0, 0.2);
  border: 2px solid #334155;
}

.dark-mode-toggle.light .toggle-thumb {
  background: #f8fafc;
  border-color: #e2e8f0;
  box-shadow: 
    0 2px 8px rgba(0, 0, 0, 0.15),
    0 1px 4px rgba(0, 0, 0, 0.1);
}

.dark-mode-toggle.dark .toggle-thumb {
  transform: translateY(-50%) translateX(40px);
}

.moon-icon {
  color: #e2e8f0;
  transition: all 0.3s ease;
  filter: drop-shadow(0 0 2px rgba(226, 232, 240, 0.5));
}

.dark-mode-toggle.light .moon-icon {
  color: #475569;
  filter: drop-shadow(0 0 2px rgba(71, 85, 105, 0.3));
  transform: rotate(-15deg);
}

.dark-mode-toggle.dark .moon-icon {
  transform: rotate(0deg);
  filter: drop-shadow(0 0 4px rgba(226, 232, 240, 0.8));
}

/* Smooth glow effect */
.dark-mode-toggle.dark .toggle-track::before {
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  background: linear-gradient(135deg, #667eea, #764ba2);
  border-radius: 22px;
  z-index: -1;
  opacity: 0.6;
  filter: blur(8px);
  transition: opacity 0.4s ease;
}

.dark-mode-toggle.light .toggle-track::before {
  opacity: 0;
}

/* Ripple effect on click */
.dark-mode-toggle:active .toggle-track {
  transform: scale(0.98);
}

.dark-mode-toggle:active .toggle-thumb {
  transform: translateY(-50%) translateX(40px) scale(0.95);
}

.dark-mode-toggle.light:active .toggle-thumb {
  transform: translateY(-50%) scale(0.95);
}
